module.exports = {"de":{"translation":{"Look through the window":"Guck durch das Fenster","Language":"Sprache","Joey's Blog":"Das Blog von Joey","I am passionate about development in Python and Data Science.":"Ich bin begeistert von Python Softwareentwicklung und Data Science.","My hobbies are reading, coding and studying about investments.":"Ich lese, programmiere und studiere ber Investitionen gerne.","...and everyone knows I love Oscar Wilde &#38; Fernando Pessoa":"...und alle wei, dass ich Oscar Wilde &#38; Fernando Pessoa liebe","I am in love with Germany since my exchange in Dresden, Saxony.":"Ich bin seit meinem Austausch in Dresden (Sachsen) verliebt in Deutschland.","PS: I &hearts; Spekulatius":"PS: Ich &hearts; Spekulatius","Technologies I used to develop this website":"Ich habe diese Technologien verwendet, um diese Website zu entwickeln","Other Technologies I have experience with":"Andere Technologien, mit denen ich Erfahrung habe","since":"seit","Previous":"Frher","Next":"Nchster"}},"en":{"translation-bio":{"#bio-literature-card":"I am fascinated by the written word. I have a lovely time reading poetry, but I feel like I can relate more to Portuguese and Brazilian works mostly for motives of culture and rhythm of our language. For plays, short stories and novels, most of my favorites titles are actually from the UK, France, Italy and some from the US and Brazil, naturally.","#bio-meetup-card":"I enjoy learning new things and getting to know people who are interested in similar Tech topics. Joining \"Meetups\" was just an obvious thing for me. The main groups I join are: <br>- PyData Berlin <br>- Python Users Berlin","#bio-investments-card":"Early in life, I was taught by dad (and life) to save for my future and I also saw men and women in my family having their own small businesses but I never had someone to teach me how to invest, so I now explore that by myself. However, my #1 dream is: be responsible for the creation of many jobs in a company people will be happy to work for."},"translation-data-visualization":{"#data-visualization-intro":"Working with data, exploring a dataframe, ploting interesting findings... I just love it! When I am not learning about Machine Learning techniques and technologies, I am playing with Data Visualization tools. I just find it very exciting how much information you can extract from a good dataset (specially when I am involved since the data collection process) and how much data visualization strategies help people better understand our data."},"translation-portfolio":{"#portfolio-this-website":"This website was created with VueJs and Bootstrap 4. For a full list of the technologies I used here, visit the TechStack page.","#portfolio-coding-challenges":"I have my most recent coding challenges solutions (mostly in C or Python) in my <a href='https://github.com/Joeffison/coding_challenges#coding-challenges' target='__blank'>Github repository<a/>.","#portfolio-ml-builder":"In 2017 I created a Proof of Concept of an idea I had to help students get started exploring Machine Learning. I used Python/Django and Angular 2. The full code source is on my Github and a mocked version is available on <a href='https://joeffison.github.io/MachineLearningBuilder/' target='__blank'>https://joeffison.github.io/MachineLearningBuilder<a/>","#portfolio-instagram-utility":"Website built with Angular 2 to provide some features not officially offered by Instagram. <a href='https://joeffison.github.io/InstagramUtility/' target='__blank'>https://joeffison.github.io/InstagramUtility<a/>","#portfolio-academic-research":"During my Research, our team at SPLab created the \"RealTime PCO\" tool (to run tests in Embedded Real-Time Sysytems) which led to our paper \"Automating Test Case Creation and Execution for Embedded Real-time Systems\" describing our proposed process to test critical systems, whose time constraints are highly important (e.g., airplaines systems)."},"translation":{}},"pt":{"translation-bio":{"#bio-literature-card":"Eu sou fascinado pela palavra escrita. Ler poesia me d um prazer imenso. Embora para a poesia eu prefira as escritas em portugus, eu tambm aprecio a literatura originria de outros lugares (e.g., Reino Unido, Frana, Itlia e Estados Unidos).","#bio-meetup-card":"Eu gosto de aprender coisas novas e conhecer pessoas interessadas em tpicos tcnicos similares, ento participar de \"Meetups\" foi apenas natural. Os principais grupos dos quais eu participo so: <br>- PyData Berlin <br>- Python Users Berlin","#bio-investments-card":"Logo cedo, eu fui ensinado pelo meu pai (e pela vida) a poupar para o meu futuro e tambm vi homens e mulheres na minha famlia abrirem os seus prprios empreendimentos, mas eu nunca tive algum que pudesse me ensinar a investir, por isso agora exploro isso eu mesmo. No entanto, meu sonho #1 : ser responsvel pela criao de empregos em uma empresa na qual as pessoas tero prazer em trabalhar."},"translation-data-visualization":{"#data-visualization-intro":"Trabalhar com dados, explorar dataframes, criar grficos com descobertas interessantes... eu simplesmente adoro tudo isso! Quando eu no estou estudando sobre novas tecnologias ou tcnicas de Aprendizagem de Mquina, eu gosto de brincar com ferramentas de Visualizao de Dados. Eu acho muito estimulante quanta informao pode ser extrada de um bom conjunto de dados (especialmente se eu estiver envolvido desde a coleta desses dados) e o quanto as estratgias de visualizao de dados nos ajudam a melhor compreender o que  evidenciado pelos dados."},"translation-portfolio":{"#portfolio-this-website":"Este site foi criado do zero, com VueJs e Bootstrap 4. Para a lista completa de tecnologias que eu usei, acesse a pgina TechStack.","#portfolio-coding-challenges":"As minhas solues mais recentes para desafios de programao (em sua maioria em C ou Python) esto no meu <a href='https://github.com/Joeffison/coding_challenges#coding-challenges' target='__blank'>repositrio do Github<a/>.","#portfolio-ml-builder":"Em 2017, eu construi uma \"Prova de Conceito\" de uma ideia que tive para ajudar estudantes no incio de suas exploraes em Machine Learning. Para tal, eu usei Python/Django e Angular 2. Todo o cdigo fonte est no meu Github e uma verso (mock) est disponvel em <a href='https://joeffison.github.io/MachineLearningBuilder/' target='__blank'>https://joeffison.github.io/MachineLearningBuilder<a/>","#portfolio-instagram-utility":"Website criado com Angular 2 para fornecer algumas ferramentas ainda no disponveis oficialmente pelo Instagram. <a href='https://joeffison.github.io/InstagramUtility/' target='__blank'>https://joeffison.github.io/InstagramUtility<a/>","#portfolio-academic-research":"Durante meu perodo de Pesquisa, nosso time do SPLab criou o \"RealTime PCO\" (ferramenta para testar sistemas crticos embarcados de tempo-real) que esultou no artigo cientfico \"Automating Test Case Creation and Execution for Embedded Real-time Systems\" descrevendo o processo proposto para testar sistemas crticos, para os quais os requisitos de tempo so extremamente importantes (e.g., sistemas de avies)."},"translation":{"Look through the window":"Olhe pela janela","Language":"Idioma","Joey's Blog":"O Blog do Joey","I am passionate about development in Python and Data Science.":"Eu sou fascinado por desenvolvimento em Python e DataScience.","My hobbies are reading, coding and studying about investments.":"Meus hobbies so ler, programar e estudar sobre investimentos.","...and everyone knows I love Oscar Wilde &#38; Fernando Pessoa":"...e todos sabem quanto eu amo Oscar Wilde e Fernando Pessoa","I am in love with Germany since my exchange in Dresden, Saxony.":"Eu sou apaixonado pela Alemanha desde o meu intercmbio em Dresden, Saxnia.","PS: I &hearts; Spekulatius":"PS: Eu &hearts; Spekulatius","Technologies I used to develop this website":"Tecnologias que eu usei para criar este site","Other Technologies I have experience with":"Outras Tecnologias com as quais tenho experincia","since":"desde","Previous":"Anterior","Next":"Prximo","This website":"Este site","Coding Challenges":"Desafios de Programao","Academic Research":"Pesquisa Acadmica","Work Experience":"Experincia Profissional","Academic Experience":"Experincia Acadmica","Library":"Biblioteca","Check out my Portfolio":"Veja o meu Portfolio","Data Science":"Cincia de Dados"}}}


