function getDaily (stock) {
  return new Promise(function (resolve, reject) {
    setTimeout(function () {
      let response = {}

      if (stock === 'AAPL34.SAO') {
        response.data = AAPL34_MOCK
      } else {
        response.data = ITSA4_MOCK
      }

      resolve(response)
    }, 100)
  })
}

export default {
  get: getDaily
}



